import React, {useState, useEffect} from 'react'
import { saveInfoCifrado, getCifrados, deleteCifrado } from '../aplication/fnData';
import CryptoJS from 'crypto-js';
import Swal from 'sweetalert2';

export const MetodoAES = () => {
/*
*
* Funcion para cifrar
*
*/
  const cifrar = (password, clave) =>{
    var textoCifrado = CryptoJS.AES.encrypt(password, clave).toString();
    return textoCifrado;
  }

/*
*
* funcion para descifrar
*
*/
  const descifrar = (mensajeCifrado, clave) =>{
    var bytes = CryptoJS.AES.decrypt(mensajeCifrado, clave);
    var textoDescifrado =bytes.toString(CryptoJS.enc.Utf8);
    return textoDescifrado;
  }

/*
*
* Formulario para descifrar los datos, llama a la funcion de descifrado
*
*/

  const descifrarDatos = async(e) =>{
    e.preventDefault(); let target = e.target;

    let textoCifrado      = target.textoCifrado.value;
    let llave          = target.llave.value;

    var textodescifrado = descifrar(textoCifrado, llave);

    e.target.reset();
    

    Swal.fire(
      'La contrasea cifrada es: ',
      `${textodescifrado}`,
    )
  }

/*
*
*Firebase para base de datos (envia datos del formulario a la base)
*
*/
  const [datos, setDatos] = useState(null);
  //const [personId, setPersonId] = useState(null);
  useEffect(() => {
    getCifradosData();
  }, [])

  const getCifradosData = async() =>{
    const p = await getCifrados();
    setDatos(p.docs);
  }  

  const removeDato = async(datoId)=>{
    await deleteCifrado(datoId);
    getCifradosData();
  }

  const saveDatosInfo = async(e) =>{
    e.preventDefault(); let target = e.target;
    let nombre         = target.nombre.value;
    let apellidos      = target.apellidos.value;
    let edad           = target.edad.value;
    let correo         = target.correo.value;
    let password       = target.password.value;
    let clave          = target.clave.value;

    var textocifrado = cifrar(password, clave);
    
    await saveInfoCifrado(nombre, apellidos, edad, correo, textocifrado, clave);
    e.target.reset();
    await getCifradosData();

    Swal.fire({
      position: 'center-center',
      icon: 'success',
      title: 'Datos Guardados Correctamente',
      showConfirmButton: false,
      timer: 1500
    })
  }

  return (
    <>
    <article className="peli-item">

      <form onSubmit={saveDatosInfo}>
        <h3 className="title">Metodo AES</h3>

        <label className="description">Nombre: </label>                    
        <input type="text" name="nombre" placeholder='Nombre' autoComplete="off" required></input><br/><br/>

        <label className="description">Apellidos: </label>                    
        <input type="text" name="apellidos" placeholder='Apellidos' autoComplete="off" required></input><br/><br/>

        <label className="description">Edad: </label>                    
        <input type="number" name="edad" placeholder='Edad' autoComplete="off" required></input><br/><br/>

        <label className="description">Correo: </label>                    
        <input type="email" name="correo" placeholder='Correo electronico' autoComplete="off" required></input><br/><br/>

        <label className="description">Contrasea: </label>                    
        <input type="password" name="password" placeholder='Contrasea' autoComplete="off" required></input><br/><br/>

        <label className="description">Ingresa la clave: </label>
        <input type="text" name="clave" placeholder='clave' autoComplete="off" required /><br/><br/>

        <br/>
        <input className="edit" type="submit" name="submit" value="Cifrar"></input><br/><br/>
      </form>

      <table border="1" align="center" bordercolor="blue" cellSpacing="0">
      <caption>Datos almacenados</caption>
          <tr>
            <td width="200">Nombre</td>
            <td width="200">Apellidos</td>
            <td width="200">Edad</td>
            <td width="200">correo</td>
            <td height="30" width="200">Contrasea</td>
            <td width="200">clave</td>
            <td width="200">Accin</td>
          </tr>
      {
        datos && datos.map(p =>
          <tr key={p.id}>
            <td>{p.data().nombre}</td>
            <td>{p.data().apellidos}</td>
            <td>{p.data().edad}</td>
            <td>{p.data().correo}</td>
            <td height="25" width="200"><div style={{overflowX: "scroll", width: "200px"}}>{p.data().mensaje}</div></td>
            <td>{p.data().clave}</td>
            <td><button className='delete' onClick={()=>removeDato(p.id)}>Eliminar</button></td>
        </tr>)
      }
      </table>
    </article>

    {/*
    *
    *
    *Formulario para el descifrado
    * 
    * *
    */
    }

    <article className="peli-item">
      <form onSubmit={descifrarDatos}>
        <h3 className="title">Metodo AES</h3>
        <p className="description">Ingresa la contrasea cifrada: </p>                    
        <textarea type="text" name="textoCifrado" placeholder='contrasea cifrado' autoComplete="off" required style={{minWidth: "350px", minHeight: "50px"}}></textarea><br/>

        <br/>
        <label className="description">Ingresa la clave: </label>
        <input type="text" name="llave" placeholder='clave' autoComplete="off" required />

        <br/>
        <input className="edit" type="submit" name="submit" value="Descifrar"></input><br/><br/>
      </form>
    </article>
    </>
  )
}
