import React from 'react'
import {Routes, Route, BrowserRouter, Navigate } from 'react-router-dom';
import { Inicio } from '../components/Inicio';
import { Aviso } from '../components/Layout/Aviso';
import { Footer } from '../components/Layout/Footer';
import { HeadNav } from '../components/Layout/HeadNav';
import { MetodoAES } from '../components/MetodoAES';
import { MetodoHash1 } from '../components/MetodoHash1';
import { MetodoHash2 } from '../components/MetodoHash2';
import { MetodoPropio } from '../components/MetodoPropio';
import { MetodoRSA } from '../components/MetodoRSA';

export const MisRutas = () => {
  return (
    <div className='layout'>

    <BrowserRouter>
        {/**HEADER Y NAVEGACION */}
        <HeadNav/>

        {/**CONTENIDO CENTRAL */}
        <section className="content">
            <Routes>
                <Route path="/" element={<Navigate to="/inicio" />} />
                <Route path="/inicio" element={<Inicio/>} />
                <Route path="/metodo-aes" element={<MetodoAES/>} />
                <Route path="/metodo-rsa" element={<MetodoRSA/>} />
                <Route path="/metodo-hash1" element={<MetodoHash1/>} />
                <Route path="/metodo-hash2" element={<MetodoHash2/>} />
                <Route path="/metodo-propio" element={<MetodoPropio/>} />
                <Route path='/aviso' element={<Aviso/>} />
                <Route path="*" element={"Pagina no encontrada"} />
            </Routes>
        </section>
        

        {/**FOOTER */}
        <Footer/>
    </BrowserRouter>
    </div>
    
  )
}
