import React from 'react'
import { NavLink } from 'react-router-dom'
import logo from '../../images/logo.png'

export const HeadNav = () => {
  return (
    <>
        <header className="header">
            <img src={logo} alt="logo"/> 
            <h1>Mtodos de Encriptacin</h1>
        </header>

        <nav className="nav">
            <ul>
                <li>
                    <NavLink to="/inicio"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >Inicio</NavLink>
                </li>
                <li>
                    <NavLink to="/metodo-aes"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >AES</NavLink>
                </li>
                <li>
                    <NavLink to="/metodo-rsa"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >RSA</NavLink>
                </li>
                <li>
                    <NavLink to="/metodo-hash1"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >Hash1</NavLink>
                </li>
                <li>
                    <NavLink to="/metodo-hash2"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >Hash2</NavLink>
                </li>
                <li>
                    <NavLink to="/metodo-propio"
                        className={({isActive}) => isActive ? "active" : ""} 
                    >Propios</NavLink>
                </li>
            </ul>
        </nav>
    </>
  )
}
