import React, {useState, useEffect} from 'react'
import { saveInfoCifrado, getCifrados, deleteCifrado } from '../aplication/fnMetodoRSA';
import { JSEncrypt } from "jsencrypt";
import Swal from 'sweetalert2';

export const MetodoRSA = () => {

/*
*
*Firebase para base de datos (envia datos del formulario a la base)
*
*/
const [datos, setDatos] = useState(null);
//const [personId, setPersonId] = useState(null);
useEffect(() => {
  getCifradosData();
}, [])

const getCifradosData = async() =>{
  const p = await getCifrados();
  setDatos(p.docs);
}  

const removeDato = async(datoId)=>{
  await deleteCifrado(datoId);
  getCifradosData();
}

const saveDatosInfo = async(e) =>{
  e.preventDefault(); let target = e.target;
  let nombre       = target.nombre.value;
  let apellidos    = target.apellidos.value;
  let edad         = target.edad.value;
  let correo       = target.correo.value;
  let mensaje      = target.mensaje.value;

  // Start our encryptor.
  var encrypt = new JSEncrypt();

  // Copied from https://github.com/travist/jsencrypt
  var publicKey = `
  -----BEGIN PUBLIC KEY-----
  MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQDlOJu6TyygqxfWT7eLtGDwajtN
  FOb9I5XRb6khyfD1Yt3YiCgQWMNW649887VGJiGr/L5i2osbl8C9+WJTeucF+S76
  xFxdU6jE0NQ+Z+zEdhUTooNRaY5nZiu5PgDB0ED/ZKBUSLKL7eibMxZtMlUDHjm4
  gwQco1KRMDSmXSMkDwIDAQAB
  -----END PUBLIC KEY-----`;

  // Copied from https://github.com/travist/jsencrypt
  var privateKey = `
  -----BEGIN RSA PRIVATE KEY-----
  MIICXQIBAAKBgQDlOJu6TyygqxfWT7eLtGDwajtNFOb9I5XRb6khyfD1Yt3YiCgQ
  WMNW649887VGJiGr/L5i2osbl8C9+WJTeucF+S76xFxdU6jE0NQ+Z+zEdhUTooNR
  aY5nZiu5PgDB0ED/ZKBUSLKL7eibMxZtMlUDHjm4gwQco1KRMDSmXSMkDwIDAQAB
  AoGAfY9LpnuWK5Bs50UVep5c93SJdUi82u7yMx4iHFMc/Z2hfenfYEzu+57fI4fv
  xTQ//5DbzRR/XKb8ulNv6+CHyPF31xk7YOBfkGI8qjLoq06V+FyBfDSwL8KbLyeH
  m7KUZnLNQbk8yGLzB3iYKkRHlmUanQGaNMIJziWOkN+N9dECQQD0ONYRNZeuM8zd
  8XJTSdcIX4a3gy3GGCJxOzv16XHxD03GW6UNLmfPwenKu+cdrQeaqEixrCejXdAF
  z/7+BSMpAkEA8EaSOeP5Xr3ZrbiKzi6TGMwHMvC7HdJxaBJbVRfApFrE0/mPwmP5
  rN7QwjrMY+0+AbXcm8mRQyQ1+IGEembsdwJBAN6az8Rv7QnD/YBvi52POIlRSSIM
  V7SwWvSK4WSMnGb1ZBbhgdg57DXaspcwHsFV7hByQ5BvMtIduHcT14ECfcECQATe
  aTgjFnqE/lQ22Rk0eGaYO80cc643BXVGafNfd9fcvwBMnk0iGX0XRsOozVt5Azil
  psLBYuApa66NcVHJpCECQQDTjI2AQhFc1yRnCU/YgDnSpJVm1nASoRUnU8Jfm3Oz
  uku7JUXcVpt08DFSceCEX9unCuMcT72rAQlLpdZir876
  -----END RSA PRIVATE KEY-----`

  // Assign our encryptor to utilize the public key.
  encrypt.setPublicKey(publicKey);

  // Perform our encryption based on our public key - only private key can read it!
  var encrypted = encrypt.encrypt(mensaje);
  
  await saveInfoCifrado(nombre, apellidos, edad, correo, encrypted);
  e.target.reset();
  await getCifradosData();

  Swal.fire({
    position: 'center-center',
    icon: 'success',
    title: 'Los datos han sido cifrados y guardados correctamente',
    showConfirmButton: false,
    timer: 1500
  })
}




  /*
  *
  *Funcion para descifrar la informacin
  *
  */
  const descifrarInfo = (e) =>{
    e.preventDefault(); let target = e.target;
    let mensajeCifrado = target.mensajeCifrado.value;

    // Start our encryptor.
    var encrypt = new JSEncrypt();

    // Copied from https://github.com/travist/jsencrypt
    var publicKey = `
    -----BEGIN PUBLIC KEY-----
    MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQDlOJu6TyygqxfWT7eLtGDwajtN
    FOb9I5XRb6khyfD1Yt3YiCgQWMNW649887VGJiGr/L5i2osbl8C9+WJTeucF+S76
    xFxdU6jE0NQ+Z+zEdhUTooNRaY5nZiu5PgDB0ED/ZKBUSLKL7eibMxZtMlUDHjm4
    gwQco1KRMDSmXSMkDwIDAQAB
    -----END PUBLIC KEY-----`;

    // Copied from https://github.com/travist/jsencrypt
    var privateKey = `
    -----BEGIN RSA PRIVATE KEY-----
    MIICXQIBAAKBgQDlOJu6TyygqxfWT7eLtGDwajtNFOb9I5XRb6khyfD1Yt3YiCgQ
    WMNW649887VGJiGr/L5i2osbl8C9+WJTeucF+S76xFxdU6jE0NQ+Z+zEdhUTooNR
    aY5nZiu5PgDB0ED/ZKBUSLKL7eibMxZtMlUDHjm4gwQco1KRMDSmXSMkDwIDAQAB
    AoGAfY9LpnuWK5Bs50UVep5c93SJdUi82u7yMx4iHFMc/Z2hfenfYEzu+57fI4fv
    xTQ//5DbzRR/XKb8ulNv6+CHyPF31xk7YOBfkGI8qjLoq06V+FyBfDSwL8KbLyeH
    m7KUZnLNQbk8yGLzB3iYKkRHlmUanQGaNMIJziWOkN+N9dECQQD0ONYRNZeuM8zd
    8XJTSdcIX4a3gy3GGCJxOzv16XHxD03GW6UNLmfPwenKu+cdrQeaqEixrCejXdAF
    z/7+BSMpAkEA8EaSOeP5Xr3ZrbiKzi6TGMwHMvC7HdJxaBJbVRfApFrE0/mPwmP5
    rN7QwjrMY+0+AbXcm8mRQyQ1+IGEembsdwJBAN6az8Rv7QnD/YBvi52POIlRSSIM
    V7SwWvSK4WSMnGb1ZBbhgdg57DXaspcwHsFV7hByQ5BvMtIduHcT14ECfcECQATe
    aTgjFnqE/lQ22Rk0eGaYO80cc643BXVGafNfd9fcvwBMnk0iGX0XRsOozVt5Azil
    psLBYuApa66NcVHJpCECQQDTjI2AQhFc1yRnCU/YgDnSpJVm1nASoRUnU8Jfm3Oz
    uku7JUXcVpt08DFSceCEX9unCuMcT72rAQlLpdZir876
    -----END RSA PRIVATE KEY-----`

    // Assign our encryptor to utilize the public key.
    encrypt.setPublicKey(publicKey);

    // Decrypt with the private key...
    var decrypt = new JSEncrypt();
    decrypt.setPrivateKey(privateKey);
    var uncrypted = decrypt.decrypt(mensajeCifrado);
    
    Swal.fire(
      'La contrasea es: ',
      `${uncrypted}`,
    )
    
    e.target.reset();
  }

  return (
    <>
    <article className="peli-item">

    <form onSubmit={saveDatosInfo}>
      <h3 className="title">Metodo RSA</h3>

      <label className="description">Nombre: </label>                    
      <input type="text" name="nombre" placeholder='Nombre' autoComplete="off" required></input><br/><br/>

      <label className="description">Apellidos: </label>                    
      <input type="text" name="apellidos" placeholder='Apellidos' autoComplete="off" required></input><br/><br/>

      <label className="description">Edad: </label>                    
      <input type="number" name="edad" placeholder='Edad' autoComplete="off" required></input><br/><br/>

      <label className="description">Correo: </label>                    
      <input type="email" name="correo" placeholder='Correo' autoComplete="off" required></input><br/><br/>

      <label className="description">Contrasea: </label>                    
      <input type="password" name="mensaje" placeholder='Contrasea' autoComplete="off" required></input><br/><br/>
      
      <input className="edit" type="submit" name="submit" value="Cifrar"></input><br/><br/>
    </form>

    <table border="1" align="center" bordercolor="blue" cellSpacing="0">
    <caption>Datos almacenados</caption>
        <tr>
          <td width="200">Nombre</td>
          <td width="200">Apellidos</td>
          <td width="200">Edad</td>
          <td width="200">Correo</td>
          <td height="30" width="200">Contrasea</td>
          <td width="200">Accin</td>
        </tr>
    {
      datos && datos.map(p =>
        <tr key={p.id}>
          <td>{p.data().nombre}</td>
          <td>{p.data().apellidos}</td>
          <td>{p.data().edad}</td>
          <td>{p.data().correo}</td>
          <td height="25" width="200"><div style={{overflowX: "scroll", width: "200px"}}>{p.data().mensaje}</div></td>
          <td><button className='delete' onClick={()=>removeDato(p.id)}>Eliminar</button></td>
      </tr>)
    }
    </table>
    </article>


    <article className="peli-item">
      <form onSubmit={descifrarInfo}>
          <h3 className='title'>Metodo RSA</h3>
          <p className="description">Ingresa la contrasea a descifrar: </p>                    
          <textarea type="text" name="mensajeCifrado" placeholder='contrasea cifrada' autoComplete="off" required style={{minWidth: "350px", minHeight: "50px"}}></textarea><br/>
          <input type="submit" value="enviar"/>
        </form>
    </article>
    </>
  )
}
