import { db } from "./firebase";
import { collection, getDocs, query, doc, addDoc, deleteDoc, updateDoc } from "firebase/firestore"

//ALTA
export const saveInfoCifrado = (nombre, apellidos, edad, correo, mensaje) =>{
    addDoc(collection(db, 'propio'), { 
        nombre, 
        apellidos, 
        edad, 
        correo, 
        mensaje, 
    });
}

//CONSULTA
export const getCifrados = async() =>{
    const result = await getDocs(query(collection(db, 'propio')));
    return result;
}

//REMOVE
export const deleteCifrado = async(id) =>{
    await deleteDoc(doc(db, 'propio', id));
}

//UPDAT4
export const updateData = async(id, titulo, subtitulo, descripcion, autor, urlImg) =>{
    await updateDoc(doc(db, 'metodoaes', id), {
        titulo,
        subtitulo,
        descripcion,
        autor,
        fechaSubida: `${new Date().getDate()} - ${new Date().getMonth()} - ${new Date().getFullYear()}`,
        urlImg,
    });
}



